import React from 'react'

const Comments = ({comments, loading}) => {
if(loading){
    return <h2 className="spinner-border"></h2>

}
const styles = {'flex-basis': '20%', width:"20%", height:'37rem', 'margin':'0 2rem 2rem 0rem'};
const style2 = {display:'flex', 'flex-direction':'row', 'justify-content': 'space-between', 'flex-wrap':'wrap' }
    return(
            <div style={style2}> 
                {comments.map(comment =>(
                    <div key={comment.id} className="card" style={styles}>
                    <img src={"https://robohash.org/" + comment.email +".png"} className="card-img-top" alt="..."/>
                    <div className="card-body">
                      <h5 className="card-title text-center">{comment.email}</h5>
                      <p className="card-text">{comment.body}</p>
                      <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                  </div>
                 
                    
                ))}
        </div>
    )
}

export default Comments;