import React from 'react'

const Dress= ()=>{
    return(
<div id="dress">
        <div id="dress-left">
                    <img class="img" src="images/dress_measurement_keep_track.png" alt="Dress Measurement" /> 
         </div> 
         <div id="dress-right" >
             <div>
                  <h1><a href="#"> Dress Measurement </a></h1>
                    <p>A convenient app for small business owners in the fashion industry to take measurements and keep track of their clients data! </p>
                    <input type="button" value="Read More"/>
                
             </div>
                   
        </div>

 </div>
            
    );

}
export default Dress;